<template>
  	<div class="login-page" :style="clientHeight">
		<div class="title-logo">
			<img :src="loginHeadBack" alt="">
		</div>
		<div class="input">
			<div class="input-user">
				<img :src="loginPhone" alt=""><input type="text" v-model.trim="qdsPhoneNum" placeholder="请输入用户名" :maxlength="11">
			</div>
			<div class="input-user">
				<img :src="loginLock" alt=""><input type="password" v-model.trim="qdsCheckPass" @keyup.enter.native="login" placeholder="请输入密码">
			</div>
		</div>
		<div class="login-btn-box">
			<span @click="login">登录</span>
		</div>
        <div class="footer">
            <span @click="goRegister">立即注册 <i class="arrow-right"></i></span><span class="line">|</span><span @click="goForgetPassword">忘记密码 <i class="arrow-right"></i></span>
        </div>
  	</div>
</template>
<script>
import loginTitle from "@/assets/qds-img/login-title.png";
import loginHeadBack from "@/assets/qds-img/login_head_back.png";
import loginPhone from "@/assets/qds-img/login-phone.png";
import loginLock from "@/assets/qds-img/login-lock.png";
import { Notify} from 'vant';
export default {
	data() {
			return{
				qdsGlobalApiUrl: domain.qdsDomain,       // 快招渠道商全局域名
                qdsPhoneNum: '',
                qdsCheckPass: '',
				show: false,
				loginTitle: loginTitle,
				loginPhone: loginPhone,
                loginLock: loginLock,
                loginHeadBack: loginHeadBack,
				clientHeight: {
					height: ""
                },
                authAState: '',
			}
	},
	created() {
		window.addEventListener('resize', this.getHeight);
		this.getHeight()
	},
	destroyed(){
        window.removeEventListener('resize', this.getHeight)
    },
	mounted() {
		
	},
	components: {
        Notify
	},
	methods: {
		login() {
            let that = this
            if(that.qdsPhoneNum == '') {
                Notify({
                    message: '请输入手机号码',
                    duration: 2000,
                    background: '#FF6262'
                });
                return false
            }else if(!(/^1[0-9]{10}$/.test(that.qdsPhoneNum))) {
                Notify({
                    message: '手机号格式不对',
                    duration: 2000,
                    background: '#FF6262'
                });
                return false
            }
            if(that.qdsCheckPass == '') {
                Notify({
                    message: '请输入密码',
                    duration: 2000,
                    background: '#FF6262'
                });
                return false
            }
			that.axios(that.qdsGlobalApiUrl + 'notValidate/login', {
				method: 'post',
				params: {
                    userName: that.qdsPhoneNum,
                    passWord: that.qdsCheckPass
				}
			})
			.then(function (response) { 
                if(response.data.code == '200') {
                    that.authAState =  response.data.entity.corpUser.certificationStatus
                    if(that.authAState == null || that.authAState == '' || that.authAState == undefined) {   
                        Notify({
                            message: '快招渠道商登录异常，请拨打021-22876396联系管理员',
                            duration: 2000,
                            background: '#FF6262'
                        });
                    }else{
                        // 1未认证  2认证中  3认证成功 4认证失败
                        localStorage.setItem('qdsSCorpUserId',response.data.entity.corpUser.id)
                        localStorage.setItem('qdsMobile',response.data.entity.corpUser.mobile)
                        sessionStorage.setItem('qdsMCertificationStatus',response.data.entity.corpUser.certificationStatus)             //认证状态数字 
                        var dataCookie = response.data.entity.token;
                        var tokenVal = encodeURIComponent(dataCookie);
                        if(location.href.indexOf("m.qh.uat.youlanw.com") == -1) {
                            document.cookie = "qdsMToken" + "=" + tokenVal + ";path=/"
                        }else {
                            document.cookie = "qdsMToken" + "=" + tokenVal + ";domain=m.qh.uat.youlanw.com;path=/";
                        }
                        // 职位刷新弹窗是否显示的判断开始
                        var localUserID = response.data.entity.corpUser.id
                        var qdslocalSCorpUserId = localStorage.getItem('qdslocalSCorpUserId'+localUserID)                               //用来判断是否需要显示带有不在提示的通知的用户id
                        var closeRefreshStateTip = localStorage.getItem('closeRefreshShelfTipLstorage'+localUserID)             //职位列表-刷新弹窗-用来判断是否需要显示刷新弹窗状态值closeRefreshShelfTipLstorage
                        if(qdslocalSCorpUserId && closeRefreshStateTip) {                  //职位管理刷新弹窗不在提示，每个账号存储一个带有当前用户id和closeWaitShelfTipLstorage，
                            if(Number(qdslocalSCorpUserId) == response.data.entity.corpUser.id && closeRefreshStateTip == 'true') {
                                localStorage.setItem('qdslocalSCorpUserId'+localUserID,response.data.entity.corpUser.id)                 //用来作为职位管理是否显示刷新弹窗中的判断条件
                                localStorage.setItem('closeRefreshShelfTipLstorage'+localUserID,'true')
                            }else {
                                localStorage.setItem('qdslocalSCorpUserId'+localUserID,response.data.entity.corpUser.id)                 //用来作为职位管理是否显示刷新弹窗中的判断条件
                            }
                        }else{
                            localStorage.setItem('qdslocalSCorpUserId'+localUserID,response.data.entity.corpUser.id)                      //用来作为职位管理是否显示刷新弹窗中的判断条件
                            localStorage.setItem('closeRefreshShelfTipLstorage'+localUserID,'false')
                        }  
                        // 职位刷新弹窗是否显示的判断结束
                        that.$router.push('/qdsSearchResume') 
                    }
                }else {
                    Notify({
                        message: response.data.message + '(错误码: ' + response.data.code + ')',
                        duration: 2000,
                        background: '#FF6262'
                    });
                }
                 
			})
			.catch(function (error) {
			});
		},
		goRegister() {
			this.$router.push('/qdsRegister')  
		},
		goForgetPassword() {
			this.$router.push('/qdsForgetPassword')  
		},
		getHeight(){
            this.clientHeight.height = window.innerHeight - 0 + 'px';
        },
	},
}
</script>

<style scoped>
    input{
        margin: 0;
        padding: 0;
        border: none;
    }
    img{
        vertical-align: middle;
        border: none;
    }
    .login-page {
        height: 100%;
        width: 100%;
        background: #fff;
        /* text-align: center; */
    }
    .login-page .title-logo{
        display: flex;
        justify-content: center;
        
    }
    .login-page .title-logo img {
        display: inline-block;
        /* width: 24rem; */
        width: 15rem;
        height: 10.1875rem;
    }
    .login-page .input-user {
        display: flex;
        display: -webkit-flex;
        align-items: center;
        width: 80%;
        margin: 0 auto;
        padding: 1.5625rem 0;
        border-bottom: 1px solid #e5e5e5;
    }
    .login-page .input-user img {
        width: .875rem;
        display: inline-block;
        margin-right:1.5625rem;
        margin-left:.625rem;
    }
    .login-page .login-btn-box {
        width: 80%;
        margin: 1.9375rem auto 1.5625rem auto;
        text-align: center;
    }
    .login-page .login-btn-box span{
        display: inline-block;
        width: 100%;
        border-radius: 1.375rem;
        background: #2da1a4;
        line-height: 2.75rem;
        color: #fff;
        font-size: 1.0625rem;
    }
    .login-page .footer{
        width:60% ;
        margin: 0 auto;
        display: -webkit-flex;
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        font-size: .8125rem;
        color: #818D8D;
    }
    .footer .line{
        color: #d7d7d7;
    }
    .login-page .footer span{
        display: inline-block;
    }
    .arrow-right {
        display: inline-block;
        margin-left: .5rem;
        width: .5rem;
        height: .5rem;
        border-right: 1px solid #818D8D;
        border-top: 1px solid #818D8D;
        transform: rotate(45deg)
    }
    ::-webkit-input-placeholder { /* WebKit, Blink, Edge */
        font-size: .8125rem;
        color: #818D8D;
    }
    :-moz-placeholder { /* Mozilla Firefox 4 to 18 */
        font-size: .8125rem;
        color: #818D8D;
    }
    ::-moz-placeholder { /* Mozilla Firefox 19+ */
        font-size: .8125rem;
        color: #818D8D;
    }
    :-ms-input-placeholder { /* Internet Explorer 10-11 */
        font-size: .8125rem;
        color: #818D8D;
    }
</style>




